






























































export default {
	data:{
		ontribution:{
			allContribution:'',//总贡献
			todayContribution:'',//今日贡献
			receiveContribution:''//剩余贡献
		}
		
	},
	onShow() {
		console.log('个人中心');
		//获取贡献值
		var that = this;
		let account = uni.getStorageSync('userInfo').account;
		let password = uni.getStorageSync('userInfo').password;
		const userInfo ={
				account:account,
				password:password,
					}
		const jsonString ={
			userInfo:userInfo,
			requestType:"login",
		}
		const param ={
			controllerRequestType:"loginControllerService",
			jsonString:JSON.stringify(jsonString)
		}
		
		uni.request({
			url:that.websiteUrl,
			method:'POST',
			header: {
				"content-type":"application/x-www-form-urlencoded"
			},
			data:param,
			success: (res) => {
				if(res.data.errorCode == '0000'){		
					console.log(res.data.userInfo);
					this.ontribution.allContribution = res.data.userInfo.allContribution;
					this.ontribution.todayContribution = res.data.userInfo.todayContribution;
					this.ontribution.receiveContribution = res.data.userInfo.receiveContribution;
					uni.hideLoading();
				
				}else{
					uni.showToast({
						icon:'none',
						title:res.data.errorMessage
					})
				}
			
			
			},
			fail:(res) =>{
				console.log(JSON.stringify(res));
			}
		})
	},
	
	methods:{
		changeAccont(){
			uni.showLoading({
				mask:true,
				title:'Loading'
			})
			uni.clearStorageSync('userInfo');
			uni.clearStorageSync('vipLevel');
			uni.reLaunch({
					url:'../index/index',
					success() {
						uni.hideLoading()
					}
			});
		},
		layOutApp(){
			plus.runtime.quit();
		}
	}
	
}



